<script setup>
import { ref, reactive } from 'vue';
import pixelItem from '@/components/pixelItem.vue';
import { useRouter } from 'vue-router';
defineProps({
  list: Array,
  counts: Array,
  type: String,
});
const router = useRouter();
const toDetails = (item, times) => {
  const params = {
    color: item.color,
    index: item.index,
    tokenid: item.tokenid,
    canvasid: item.canvasid,
    times,
  };
  router.push({
    path: '/nftDetails',
    query: params,
  });
};
const emit = defineEmits(['toCancel']);
const toCancel = (id, tokenId) => {
  emit('toCancel', id, tokenId);
};
</script>
<template>
  <div class="nft-list padding flex_between">
    <pixel-item
      v-for="(item, key) in list"
      :key="key"
      :pixelInfo="item"
      name="nft"
      btnType="primary"
      btnText="NFT.cancel"
      :type="type"
      :count="counts[key]"
      @toCancel="toCancel"
      @click.stop=""
    ></pixel-item>
  </div>
</template>

<style lang="scss" scoped>
.nft-list {
  flex-wrap: wrap;
}
</style>
