<script setup>
import { ref, reactive, onBeforeMount, watchEffect, watch } from 'vue';
import notice from './components/notice.vue';
import nftList from './components/nftList.vue';
import sellList from './components/sellList.vue';
import swapAlert from '@/components/swapAlert.vue';
import { commonStore } from '@/store/common';
import { Toast } from '_vant@3.4.8@vant';
import { getUserSell, getUserAll } from '@/http/api';
const common_store = commonStore();
const data = reactive({
  holdList: [],
  sellList: [],
  allList: [],
  active: 0,
  counts: [],
  sellCounts: [],
  allCounts: [],
  countTime: [],
  loading: true,
  finished: false,
  page: 0,
});
const swapData = reactive({
  alertShow: false,
  finish: false,
  success: false,
  hash: '',
  tokenid: '',
  hashType: 'cancel',
});
const getHoldList = async () => {
  data.loading = true;
  try {
    data.holdList = await common_store.allMethods
      .getUserPixel(common_store.address)
      .call();
    data.loading = false;
    common_store.setLoading(false);
    const tokenArr = [];
    data.holdList = data.holdList.map((item) => {
      return { ...item, id: item.index };
    });
    console.log(data.holdList);
    data.holdList.forEach((item) => {
      item.id = item.index;
      tokenArr.push(item.tokenid);
    });
    getSellTimes(0, tokenArr);
  } catch (error) {
    Toast(error);
  }
};
const getSellTimes = async (num, tokenArr) => {
  const res = await common_store.marketMethods.listNftCount(tokenArr).call();

  if (num == 0) {
    data.counts = data.counts.concat(res.counts_);
    data.countTime = data.countTime.concat(res.times_);
  } else if (num == 1) {
    data.sellCounts = data.sellCounts.concat(res.counts_);
  } else {
    data.allCounts = data.allCounts.concat(res.counts_);
  }
};
const getSellList = () => {
  const page = data.page * 20;
  getUserSell(common_store.address, page).then((res) => {
    data.loading = false;
    common_store.setLoading(false);
    const list = res.data.marketItemRecEntities;
    data.sellList = data.sellList.concat(list);
    data.finished = list.length < 20;
    const tokenArr = [];
    data.sellList.forEach((item) => {
      tokenArr.push(item.tokenId);
    });
    data.sellLoad = true;
    getSellTimes(1, tokenArr);
  });
};

const getUserAllList = () => {
  const page = data.page * 20;
  getUserAll(common_store.address, page).then((res) => {
    data.loading = false;
    common_store.setLoading(false);
    const list = res.data.marketItemRecEntities;
    data.allList = data.allList.concat(list);
    data.finished = list.length < 20;
    const tokenArr = [];
    data.allList.forEach((item) => {
      tokenArr.push(item.tokenId);
    });
    data.allLoad = true;
    getSellTimes(3, tokenArr);
  });
};

const onLoad = () => {
  if (data.finished) {
    return;
  }
  data.page++;
  data.loading = true;
  data.active == 1 ? getSellList() : getUserAllList();
};

onBeforeMount(() => {
  common_store.initMarketWeb3();
});
watchEffect(() => {
  if (common_store.marketEmpower && common_store.address) {
    common_store.setLoading(true);
    data.active = 0;
    getHoldList();
  }
});
watch(
  () => data.active,
  (val) => {
    common_store.setLoading(true);
    data.page = 0;
    data.loading = true;
    data.sellCounts = [];
    data.allCounts = [];
    data.counts = [];
    data.countTime = [];
    if (val == 0) {
      data.holdList = [];
      getHoldList();
    } else if (val == 1) {
      data.sellList = [];
      getSellList();
    } else {
      data.allList = [];
      getUserAllList();
    }
  }
);
const toSell = async (id) => {
  swapData.tokenid = id;
  try {
    const res = await common_store.allMethods.getApproved(id).call();
    if (res == common_store.market_address) {
      sell();
    } else {
      //授权弹框
      // const res = await common_store.allMethods
      //   .approve(common_store.market_address, pixelInfo.value.tokenid)
      //   .send({ from: common_store.address });
      common_store.setEmpowerType(2, id);
      common_store.showEmpower(true);
    }
  } catch (error) {
    console.log(error);
    Toast(error);
  }
};
const sell = async () => {
  swapData.hashType = 'sell';
  swapData.alertShow = true;
  swapData.finish = false;
  swapData.success = false;
  try {
    const sellRes = await common_store.marketMethods
      .sell(swapData.tokenid)
      .send({ from: common_store.address });
    // router.go(-1);
    swapData.finish = true;
    swapData.success = true;
    swapData.hash = sellRes.transactionHash;
    common_store.addHash(swapData.hash, 1);
  } catch (error) {
    swapData.finish = true;
    swapData.success = false;
    swapData.hash = error.message;
  }
};
const toCancel = async (id, tokenId) => {
  swapData.hashType = 'cancel';
  swapData.alertShow = true;
  swapData.finish = false;
  swapData.success = false;
  swapData.tokenid = tokenId;
  try {
    const res = await common_store.marketMethods
      .cancel(id)
      .send({ from: common_store.address });
    swapData.finish = true;
    swapData.success = true;
    swapData.hash = res.transactionHash;
    common_store.addHash(swapData.hash, 5);
  } catch (error) {
    swapData.finish = true;
    swapData.success = false;
    swapData.hash = error.message;
  }
};
const closeFn = () => {
  common_store.setLoading(true);
  swapData.alertShow = false;
  data.page = 0;
  if (data.active == 0) {
    data.holdList = [];
    data.counts = [];
    getHoldList();
  } else {
    data.sellCounts = [];
    data.sellList = [];
    getSellList();
  }
};
</script>
<template>
  <div class="mynft">
    <!-- <notice></notice> -->
    <van-tabs
      v-model:active="data.active"
      offset-top="20"
      title-active-color="#fff"
      title-inactive-color="#999999"
      animated
    >
      <van-tab :title="$t('NFT.tabs.one')" title-active-color="#fff">
        <div class="record-tabs">
          <nft-list
            :list="data.holdList"
            type="hold"
            :counts="data.counts"
            @toSell="toSell"
            :countTime="data.countTime"
          ></nft-list>
          <no-record
            v-if="data.holdList.length == 0 && !data.loading"
          ></no-record>
        </div>
      </van-tab>
      <van-tab :title="$t('NFT.tabs.two')">
        <div class="record-tabs">
          <van-list
            v-model:loading="data.loading"
            :finished="data.finished"
            :immediate-check="false"
            :loading-text="$t('loadingText')"
            @load="onLoad"
          >
            <sell-list
              :list="data.sellList"
              :counts="data.sellCounts"
              type="sell"
              @toCancel="toCancel"
              :countTime="data.countTime"
            ></sell-list>
          </van-list>
          <no-record
            v-if="data.sellList.length == 0 && !data.loading"
          ></no-record>
        </div>
      </van-tab>
      <van-tab :title="$t('NFT.tabs.three')">
        <div class="record-tabs">
          <van-list
            v-model:loading="data.loading"
            :finished="data.finished"
            :immediate-check="false"
            :loading-text="$t('loadingText')"
            @load="onLoad"
          >
            <nft-list
              :list="data.allList"
              :counts="data.allCounts"
              type="hasBuy"
              :countTime="data.countTime"
            ></nft-list>
          </van-list>
          <no-record
            v-if="data.allList.length == 0 && !data.loading"
          ></no-record>
        </div>
      </van-tab>
    </van-tabs>
  </div>
  <van-popup v-model:show="swapData.alertShow" :close-on-click-overlay="false">
    <swap-alert
      :finish="swapData.finish"
      :success="swapData.success"
      :hash="swapData.hash"
      @closeFn="closeFn"
      :type="swapData.hashType"
      :title="
        swapData.hashType == 'sell' ? 'SwapAlert.title6' : 'SwapAlert.title2'
      "
      :text="
        swapData.hashType == 'sell'
          ? 'SwapAlert.success3'
          : 'SwapAlert.success2'
      "
      :btn="swapData.hashType == 'sell' ? 'SwapAlert.btn3' : 'SwapAlert.btn2'"
    >
      <div>
        <p class="font-Four" v-if="swapData.hashType == 'sell'">
          {{
            $t('NFT.type1', {
              start: common_store.address.slice(0, 6),
              end: common_store.address.slice(-4),
              tokenId: swapData.tokenid,
            })
          }}
        </p>
        <p class="font-Four" v-if="swapData.hashType == 'cancel'">
          {{
            $t('NFT.type2', {
              start: common_store.address.slice(0, 6),
              end: common_store.address.slice(-4),
              tokenId: swapData.tokenid,
            })
          }}
        </p>
      </div>
    </swap-alert>
  </van-popup>
</template>

<style lang="scss" scoped>
.mynft {
  padding-top: 10px;
}
.record-tabs {
  max-height: calc(100vh - 70px);
  overflow-y: auto;
}
</style>
