<script setup>
import { ref, reactive, toRefs } from 'vue';
import pixelItem from '@/components/pixelItem.vue';

import { useRouter } from 'vue-router';
import { commonStore } from '@/store/common';
const common_store = commonStore();
const props = defineProps({
  list: Array,
  counts: Array,
  type: String,
  countTime: Array,
});

const { type, countTime } = toRefs(props);
const router = useRouter();
const emit = defineEmits(['toSell']);
const toDetails = (item, key) => {
  if (type.value == 'hasBuy') {
    return;
  }
  const params = {
    color: item.color,
    index: item.index,
    tokenid: item.tokenid,
    canvasid: item.canvasid,
    endDate: countTime.value[key],
    // times,
  };
  router.push({
    path: '/nftDetails',
    query: params,
  });
};
const toSell = (id) => {
  emit('toSell', id);
};
</script>
<template>
  <div class="nft-list padding flex_between">
    <pixel-item
      v-for="(item, key) in list"
      :key="key"
      :pixelInfo="item"
      name="nft"
      btnType="primary"
      btnText="NFT.sell"
      :type="type"
      :endDate="countTime[key]"
      @toSell="toSell"
      :count="counts[key]"
      @click.stop="toDetails(item, key)"
    ></pixel-item>
  </div>
</template>

<style lang="scss" scoped>
.nft-list {
  flex-wrap: wrap;
}
</style>
